.services {
  margin-top: 85px;
  position: relative;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: minmax(100px, auto);
  grid-gap: 20px;
  grid-auto-flow: dense;
  margin-bottom: 50px;
}
.services .services_box {
  display: grid;
  place-items: start;
  justify-content: center;
  padding: 12px;
  background-color: #191d2b;
  border-top: 5px solid #2e344e;
  transition: all 0.4s ease-in-out;
}
.services .services_box:hover {
  border-top-color: #037fff;
}
.services_content {
  position: relative;
}
.services_content span {
  font-size: 25px;
}
.services_content h5 {
  margin-top: 20px;
  text-transform: capitalize;
  margin-bottom: 44px;
  font-size: 30px;
}
.services_content p {
  margin-top: 20px;
  color: #a4acc4;
}
@media (max-width: 500px) {
  .services {
    grid-template-columns: repeat(1, 1fr);
  }
  .services .services_box {
    grid-column: unset !important;
    grid-row: unset !important;
  }
}
@media (max-width: 700px) {
  .services {
    grid-template-columns: repeat(1, 1fr);
  }
  .services .services_box {
    grid-column: unset !important;
    grid-row: unset !important;
  }
}
@media (max-width: 1000px) {
  .services {
    grid-template-columns: repeat(1, 1fr);
  }
  .services .services_box {
    grid-column: unset !important;
    grid-row: unset !important;
  }
}

@media (max-height: 700px) and (max-width: 1500px){
  .services_box {
    width: 96.5%;
  }  
}